// Assuming deepSeekMetadataExtractor and OpenAICompatibleChatLanguageModel are defined somewhere
// If you're using modules, you'll need to import them like below in Node.js (CommonJS or ESModule style)

// Example for CommonJS:
// const { deepSeekMetadataExtractor } = require('./deepseek-metadata-extractor');
// const { OpenAICompatibleChatLanguageModel } = require('./openai-compatible-chat-model');

function createDeepSeek(options = {}) {
  const baseURL = (options.baseURL ?? 'https://api.deepseek.com/v1').replace(/\/$/, '');

  const getHeaders = () => ({
    Authorization: `Bearer ${options.apiKey ?? process.env.DEEPSEEK_API_KEY ?? ''}`,
    ...(options.headers || {}),
  });

  const createLanguageModel = (modelId, settings = {}) => {
    return new OpenAICompatibleChatLanguageModel(modelId, settings, {
      provider: 'deepseek.chat',
      url: ({ path }) => `${baseURL}${path}`,
      headers: getHeaders,
      fetch: options.fetch,
      defaultObjectGenerationMode: 'json',
      metadataExtractor: deepSeekMetadataExtractor,
    });
  };

  const provider = (modelId, settings) => createLanguageModel(modelId, settings);

  provider.languageModel = createLanguageModel;
  provider.chat = createLanguageModel;

  provider.textEmbeddingModel = (modelId) => {
    throw new Error(`Model not found: ${modelId}`);
  };

  return provider;
}

// Export if using in Node.js environment (ESM style)
export const deepseek = createDeepSeek();
